import { Injectable } from '@angular/core';
import { Effect } from '@ngrx/effects';
import { Actions } from '@ngrx/effects';
import { Store } from '@ngrx/store';
import { switchMap, map, withLatestFrom } from 'rxjs/operators';
import { HttpClient, HttpRequest } from '@angular/common/http';

import * as RecipeActions from './recipe.actions';
import { Recipe } from '../recipe.model';
import * as fromRecipe from '../store/recipe.reducers';

@Injectable()
export class RecipeEffects {

  @Effect()
  recipeFetch = this.actions$
    .ofType(RecipeActions.FETCH_RECIPES)
    .pipe(switchMap((action: RecipeActions.FetchRecipes) => {
      return this.httpClient.get<Recipe[]>('https://ng-cook-book-38806.firebaseio.com/recipes.json', {
      observe: 'body',
      responseType: 'json'
    });
  }),
  map((recipes: Recipe[]) => {
    for (let recipe of recipes) {
      if (!recipe['ingredients']) {
        recipe['ingredients'] = [];
      }
    }
    return {
      type: RecipeActions.SET_RECIPES,
      payload: recipes
    };
  })
  );

  @Effect({dispatch: false})
  recipeStore = this.actions$
    .ofType(RecipeActions.STORE_RECIPES)
    .pipe(withLatestFrom(this.store.select('recipes')),
    switchMap(([action, state]) => {
      const request = new HttpRequest('PUT', 'https://ng-cook-book-38806.firebaseio.com/recipes.json', state.recipes, {reportProgress: true});
      return this.httpClient.request(request);
    })
    );

  constructor(
    private actions$: Actions,
    private httpClient: HttpClient,
    private store: Store<fromRecipe.FeatureState>) {}
}
